<template>
  <div>
    <el-row gutter="20">
      <el-col :span="24">
        <el-table :data="web_info">
          <el-table-column prop="webName" label="网站名"></el-table-column>
          <el-table-column prop="webPath" label="网站虚拟路径"></el-table-column>
          <el-table-column prop="webFilePath" label="网站文件路径"></el-table-column>
          <el-table-column label="操作" width="180">
            <template #default="scope">
              <el-row>
                <el-col :span="12">
                  <el-button type="primary">
                    更新
                  </el-button>
                </el-col>
                <el-col :span="12">
                  <el-button @click="delWeb(scope.row.index)">
                    删除
                  </el-button>
                </el-col>
              </el-row>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
      <el-col :span="24" style="padding-top: 20px;text-align: right">
        <el-button @click="addNewWebClick" type="primary">
          添加站点
        </el-button>
      </el-col>
    </el-row>
    <el-dialog v-model="add_web_show" title="添加站点">
      <el-form>
        <el-form-item label="网站名称">
          <el-input v-model="add_new_web_info.web_name" type="text"></el-input>
        </el-form-item>
        <el-form-item label="网站虚拟路径">
          <el-input v-model="add_new_web_info.web_path" type="text"></el-input>
        </el-form-item>
        <el-form-item label="网站文件路径">
          <el-input v-model="add_new_web_info.web_file_path" type="text"></el-input>
        </el-form-item>
      </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button @click="add_web_show = false">取消</el-button>
        <el-button type="primary" @click="addNewWeb">提交</el-button>
      </span>
      </template>
    </el-dialog>
  </div>
</template>


<script>
import axios from "axios";
import {ElMessage} from "element-plus";

export default {
  name: "sys-web-config",
  data() {
    return {
      // eslint-disable-next-line no-undef
      server: SERVER,
      web_info: [],
      add_web_show: false,
      add_new_web_info: {
        web_name: "",
        web_path: "",
        web_file_path: ""
      }
    }
  },
  methods: {
    addNewWebClick: function () {
      this.add_web_show = true;
    },
    delWeb: function (index) {
      axios.post(this.server + "/api/delWeb", {
        "web_file_path": this.web_info[index]['webFilePath']
      }).then((res) => {
        ElMessage({
          message: res.data['msg'],
          type: 'success ',
        })
      }).catch((error) => {
        ElMessage({
          message: error.response.data['msg'],
          type: 'error',
        })
      })
    },
    addNewWeb: function () {
      axios.post(this.server + "/api/addWebInfo", {
        "web_name": this.add_new_web_info.web_name,
        "web_path": this.add_new_web_info.web_path,
        "web_file_path": this.add_new_web_info.web_file_path
      }).then((res) => {
        ElMessage({
          message: res.data['msg'],
          type: 'success ',
        })
        this.reload()
        this.add_web_show = false
      }).catch((error) => {
        ElMessage({
          message: error.response.data['msg'],
          type: 'error',
        })
      })
    },
    getWebInfo: function () {
      axios.get(this.server + "/api/getWebInfo").then((res) => {
        this.web_info = res.data;
      }).catch((error) => {
        console.log(error)
      })
    },
    reload: function () {
      axios.post(this.server + "/api/reload").then((res) => {
        ElMessage({
          message: res.data['msg'],
          type: 'success',
        })
        this.getWebInfo()
      }).catch((error) => {
        console.log(error)
      })
    }
  },
  mounted() {
    this.getWebInfo();
  },
  components: {
  }
}
</script>

<style scoped>

</style>
